'use client';

import { useEffect, useState } from 'react';
import {
  getAllCustomers,
  createCustomer,
  updateCustomer,
  deleteCustomer,
} from '@/src/lib/services/customers/customerService';
import { Customer } from '@/src/lib/customers/customerTypes';
import Header from '@/src/components/landingpage/Header';
import Footer from '@/src/components/landingpage/Footer';
import UserTopMenu from '@/src/components/landingpage/UserTopMenu';
import CustomerForm from '@/src/components/customers/customerForm';
import CustomerTable from '@/src/components/customers/customerTable';
import { Timestamp } from 'firebase/firestore';
import NotificationDialog from '@/src/components/ui/NotificationDialog';

const initialCustomer = {
  userId: '',
  name: '',
  email: '',
  phone: '',
  address: '',
  dateOfBirth: null,
  driverLicense: '',
  idNumber: '',
  nationality: '',
  sex: '',
  placeOfOrigin: '',
  placeOfResidence: '',
};

export default function CustomersPage() {
  const [customers, setCustomers] = useState<Customer[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [editingCustomer, setEditingCustomer] = useState<Customer | null>(null);
  const [newCustomer, setNewCustomer] = useState<Omit<Customer, 'id' | 'createdAt' | 'updatedAt'>>(initialCustomer);

  const [dialog, setDialog] = useState({
    open: false,
    type: 'info' as 'success' | 'error' | 'info' | 'confirm',
    title: '',
    description: '',
    onConfirm: undefined as (() => void) | undefined,
  });

  const showDialog = (
    type: 'success' | 'error' | 'info',
    title: string,
    description = ''
  ) => {
    setDialog({ open: true, type, title, description, onConfirm: undefined });
  };

  const confirmDelete = (id: string) => {
    const customer = customers.find((c) => c.id === id);
    if (!customer) return;

    setDialog({
      open: true,
      type: 'confirm',
      title: `Delete ${customer.name}?`,
      description: 'This action cannot be undone.',
      onConfirm: async () => {
        try {
          await deleteCustomer(id);
          setCustomers(customers.filter((c) => c.id !== id));
          setDialog((prev) => ({ ...prev, open: false }));
          showDialog('success', 'Customer deleted successfully');
        } catch (err) {
          showDialog('error', 'Failed to delete customer');
        }
      },
    });
  };

  useEffect(() => {
    const fetchData = async () => {
      const data = await getAllCustomers();
      setCustomers(data);
    };
    fetchData();
  }, []);

  const saveCustomer = async () => {
    try {
      if (editingCustomer) {
        await updateCustomer(editingCustomer.id, newCustomer);
        setCustomers(customers.map(c => c.id === editingCustomer.id ? { ...c, ...newCustomer } : c));
        showDialog('success', 'Customer updated successfully');
        setEditingCustomer(null);
      } else {
        const created = await createCustomer(newCustomer);
        setCustomers([...customers, created]);
        showDialog('success', 'Customer added successfully');
      }
      setNewCustomer(initialCustomer);
    } catch (error) {
      console.error('‚ùå Error in saveCustomer:', error);
      showDialog('error', 'Failed to save customer');
    }
  };

  const handleEdit = (customer: Customer) => {
    const { id: _id, createdAt: _createdAt, updatedAt: _updatedAt, ...dataForForm } = customer;
    setEditingCustomer(customer);
    setNewCustomer(dataForForm);
  };

  return (
    <div className="flex flex-col min-h-screen">
      <Header />
      <UserTopMenu />
      <div className="p-6">
        <h1 className="text-2xl font-semibold mb-4 border-[#00d289] border-b-2 pb-2">Customers Management</h1>

        <CustomerForm
          editingCustomer={editingCustomer}
          newCustomer={newCustomer}
          setNewCustomer={setNewCustomer}
          onSave={saveCustomer}
          onCancel={() => {
            setEditingCustomer(null);
            setNewCustomer(initialCustomer);
          }}
        />

        <CustomerTable
          customers={customers}
          onEdit={handleEdit}
          onDelete={confirmDelete}
          searchTerm={searchTerm}
          setSearchTerm={setSearchTerm}
        />
      </div>
      <Footer />

      <NotificationDialog
        open={dialog.open}
        type={dialog.type}
        title={dialog.title}
        description={dialog.description}
        onClose={() => setDialog((prev) => ({ ...prev, open: false }))}
        onConfirm={dialog.onConfirm}
      />
    </div>
  );
}